.home {
  width: 640px;
  height: 360px;
  background: linear-gradient(114.99deg, #471ca9 -0.99%, #5736a3 54.28%, #4b2a99 78.99%);
  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
  border-radius: 20px;
  margin: 40px auto;
  padding: 40px;
}

.homeTitle {
  margin-top: 40px;
  text-align: center;
  font-family: 'Montserrat'; 
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;  
  text-transform: uppercase;
  color: #28048d;
}